/* Base */
@import url("https://fonts.googleapis.com/css2?family=Jaro:opsz@6..72&family=Roboto:wght@400;700&display=swap");
/** Media Queries **/
html {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-size: 62.2%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

.prueba {
  display: flex;
  flex-direction: column;
}
@media (min-width: 1024px) {
  .prueba {
    flex-direction: row;
  }
}

body {
  font-size: 1.6rem;
  font-family: "Jaro", sans-serif;
  background-color: #1A1A1D;
  line-height: 1.5;
}

.content {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img, picture {
  display: block;
  width: 100%;
}

h1, h2, h3 {
  text-shadow: 2px 2px 0px black, -2px -2px 0px black, 2px -2px 0px black, -2px 2px 0px black; /* Borde inferior e izquierdo */
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.btn-1 {
  background-color: #6A1E55;
  color: #fff;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  border: none;
}
.btn-1:hover {
  background-color: rgb(100.7, 28.5, 80.75);
  cursor: pointer;
}
@media (min-width: 768px) {
  .btn-1 {
    display: inline;
  }
}

.btn-2 {
  background-color: #A64D79;
  color: #fff;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  border: none;
}
.btn-2:hover {
  background-color: rgb(157.7, 73.15, 114.95);
  cursor: pointer;
}
@media (min-width: 768px) {
  .btn-2 {
    display: inline;
  }
}

/* UI */
.header {
  font-family: sans-serif;
  /*  img{
       position: absolute;
       top: 0;
       height: 50%;
   } */
  background-image: url(../../src/img/headerBg.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  height: 25rem;
  width: 101%;
}
@media (min-width: 768px) {
  .header {
    height: 50rem;
  }
}

.main-nav {
  /* P.G */
  z-index: 1;
  position: relative;
  top: 5rem; /* 17 */
  margin: 0 4rem;
  /* P.Caja */
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}
@media (min-width: 768px) {
  .main-nav {
    flex-direction: row;
    justify-content: space-between;
    top: 17rem;
  }
}
.main-nav nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}
@media (min-width: 768px) {
  .main-nav nav {
    flex-direction: row;
  }
}
.main-nav a {
  color: #000;
  font-size: 1.5rem;
  text-transform: uppercase;
  padding: 1rem;
}
@media (min-width: 1024px) {
  .main-nav a {
    font-size: 3rem;
  }
}
.main-nav .btn-2 {
  color: #fff;
}

.noneheader {
  background-image: none;
  background-color: antiquewhite;
  display: grid;
  height: 100%;
}
@media (min-width: 768px) {
  .noneheader {
    height: 15rem;
  }
}
.noneheader .main-nav {
  position: static;
  padding: 2rem;
}
.noneheader .main-nav a {
  font-size: 1.5rem;
}

.about-me {
  /* z-index: 1; */
  text-align: center;
}
@media (min-width: 768px) {
  .about-me {
    margin-top: -15rem;
  }
}
@media (min-width: 1424px) {
  .about-me {
    left: 30rem;
  }
}
.about-me h2 {
  font-size: 6rem;
  color: #fff;
}
@media (min-width: 768px) {
  .about-me h2 {
    font-size: 10rem;
  }
}
.about-me p {
  color: #fff;
  border-bottom: 1px solid #fff;
  padding: 2rem;
}

.conocimiento {
  color: #fff;
}
.conocimiento h3 {
  border-bottom: 1px solid #fff;
  padding: 2rem;
}
.conocimiento .conocimiento-lista {
  list-style: none;
}

.proyect {
  text-align: center;
}
.proyect h2 {
  color: #fff;
  font-size: 8rem;
  text-shadow: 2px 2px 0px #A64D79, -2px -2px 0px #A64D79, 2px -2px 0px #A64D79, -2px 2px 0px #A64D79; /* Borde inferior e izquierdo */
}

.proyect-box {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 1024px) {
  .proyect-box {
    flex-direction: row;
  }
}
.proyect-box .proyect-box_first {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #000;
  width: 100%;
}
.proyect-box .proyect-box_first img {
  box-shadow: 5px 5px 10px rgba(255, 253, 253, 0.5), -5px -5px 10px rgba(255, 255, 255, 0.2);
  width: 50%;
  height: 30rem;
}
.proyect-box .proyect-box_first .proyect-box_text {
  background-color: #fff;
  width: 50%;
}
.proyect-box .proyect-box_first .proyect-box_text p {
  padding: 2rem;
}

.footer {
  background-color: #fff;
  margin-top: 10rem;
  padding: 2rem;
}
.footer .footer-nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}
@media (min-width: 768px) {
  .footer .footer-nav {
    flex-direction: row;
    justify-content: space-between;
  }
}
.footer .footer-nav nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}
.footer .footer-nav nav a {
  color: #000;
  font-size: 2rem;
}
@media (min-width: 768px) {
  .footer .footer-nav nav {
    flex-direction: row;
  }
}

.contacto {
  color: #fff;
  text-align: center;
}
.contacto img {
  border-radius: 100%;
  width: 70%;
}
@media (min-width: 768px) {
  .contacto img {
    width: 40%;
  }
}
.contacto .box-contact-info {
  display: flex;
  flex-direction: column;
  gap: 3rem;
  align-items: center;
  padding: 10rem;
}
@media (min-width: 1024px) {
  .contacto .box-contact-info {
    flex-direction: row;
  }
}

.box-contact_text .contact-text {
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2rem;
}
.box-contact_text .contact-text img {
  width: 5rem;
}
.box-contact_text .contact-text a {
  color: #fff;
}

.box-contact {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 1424px) {
  .box-contact {
    flex-direction: row;
  }
}

.mision {
  background-color: green;
  height: 100%;
  width: 100%;
  border-radius: 2rem;
}
@media (min-width: 768px) {
  .mision {
    width: 20%;
  }
}
.mision p {
  padding: 2rem;
  line-height: 2;
}